"use client";

import { useState } from 'react';
import Image from 'next/image';

export default function Home() {
  const [file, setFile] = useState(null);
  const [vehicleNumber, setVehicleNumber] = useState('');
  const [area, setArea] = useState('');

  const handleFileChange = (e) => {
    const selected = e.target.files[0];
    setFile(selected);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log({
      file: file ? file.name : 'No file uploaded',
      vehicleNumber,
      area,
    });
    setFile(null);
    setVehicleNumber('');
    setArea('');
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-blue-100 flex items-center justify-center p-4">
      <div className="bg-white shadow-lg rounded-2xl p-6 w-full max-w-3xl">
        <h1 className="text-2xl font-bold text-center text-blue-700 mb-6">
          Vehicle Checkpoint Form
        </h1>

        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Upload Section */}
          <div className="flex flex-col items-center justify-center border-2 border-dashed border-blue-300 rounded-lg p-6 bg-blue-50">
            <label
              htmlFor="file-upload"
              className="cursor-pointer text-blue-600 font-medium"
            >
              {file ? file.name : "Click to upload image or video"}
              <input
                id="file-upload"
                type="file"
                accept="image/*,video/*"
                className="hidden"
                onChange={handleFileChange}
              />
            </label>
            {file && (
              <p className="text-sm text-gray-500 mt-2">
                File selected: <strong>{file.name}</strong>
              </p>
            )}
          </div>

          {/* Vehicle Number */}
          <div>
            <label
              htmlFor="vehicle-number"
              className="block text-gray-700 font-medium mb-1"
            >
              Vehicle Number
            </label>
            <input
              id="vehicle-number"
              type="text"
              value={vehicleNumber}
              onChange={(e) => setVehicleNumber(e.target.value)}
              className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-400 focus:outline-none"
              placeholder="e.g., ABC-1234"
              required
            />
          </div>

          {/* Area Dropdown */}
          <div>
            <label
              htmlFor="area"
              className="block text-gray-700 font-medium mb-1"
            >
              Select Area
            </label>
            <div className="relative">
              <select
                id="area"
                value={area}
                onChange={(e) => setArea(e.target.value)}
                className="w-full p-3 border border-gray-300 rounded-lg appearance-none focus:ring-2 focus:ring-blue-400 focus:outline-none"
                required
              >
                <option value="">Choose an area</option>
                <option value="Area 1">Area 1</option>
                <option value="Area 2">Area 2</option>
                <option value="Area 3">Area 3</option>
              </select>
              <div className="absolute right-4 top-1/2 transform -translate-y-1/2 pointer-events-none">
                <Image src="/search-icon.png" alt="Search" width={16} height={16} />
              </div>
            </div>
          </div>

          {/* Submit Button */}
          <button
            type="submit"
            className="w-full bg-blue-600 text-white py-3 rounded-lg font-semibold hover:bg-blue-700 transition-all duration-200"
          >
            Submit
          </button>
        </form>
      </div>
    </div>
  );
}
